.nav__section {
    /* background-color: var(--nav-background); */
    backdrop-filter: var(--backdrop-filter);
    /* width: 100%; */

    padding: 24px 128px;

    display: flex;
    justify-content: space-between;
    color: var(--white);
}

.logo {
    color: var(--white);
    font-size: 20px;

    font-weight: 700;
}

/* ul > li {
    list-style: none;
} */

.nav__items {
    display: flex;
    gap: 40px;
}

.nav__items a  {
    text-decoration: none;
    color: var(--white);
}

/* MEDIA SCREEN */
@media screen and (max-width: 960px) {
    .nav__section {
     padding: 24px 100px;
    }

    .section {
        padding: 24px 100px;
    }
 }

 
@media screen and (max-width: 970px) {
    .nav__section {
     padding: 24px 100px;
    }

    .section {
        padding: 24px 100px;
    }

 }

 @media screen and (max-width: 768px) {
    .nav__section {
     padding: 24px 60px;
    }

    .nav__items {
        display: none;
    }

    .section {
        padding: 24px 60px;
    }


 }

 @media screen and (max-width: 425px) {

    .nav__section {
        padding: 24px 25px;
        /* background-color: orange; */
    }

    .logo {
        font-size: 14px;
    }

    .section {
        padding: 24px 30px;
    }
 }